html {
  background-color: #ff7800;
}

.post {
  background-color: #5ec4cd;
}

.hero-backdrop, .edit-backdrop {
  z-index: 2;
  visibility: hidden;
  background-color: #00000067;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.edit-modal {
  z-index: 3;
  background-color: #5ec4cd;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 500px;
  height: 400px;
  display: flex;
  box-shadow: 0 8px 24px #0003;
}
